<template>
  <div class="params">
    <vxe-modal v-model="value" title='历史价格（双击行可将价格带入页面或者单击行选中）' @hide="close" width="1200" :esc-closable='true' destroy-on-close height="600" min-width="600" min-height="100" resize remember storage transfer>
      <div>
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="最近采购价：" size="mini">
            {{ showData.purchase_price }}
          </el-form-item>
          <el-form-item label="平均成本价：" size="mini">
            {{ showData.cost_price }}
          </el-form-item>
          <br>
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" clear-icon type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="客户名称" size="mini">
            <erpUsers v-model="params.customer_id" :searchParams='{ company_id: company_id }' />
          </el-form-item>
          <el-form-item size="mini">
            <el-button @click="onSearch" size="mini" type="primary" style="margin-left:50px;">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>

        <xTable @cellDblclick='submit' rowId='historicalPrices' @getData='dealData' @current-change='currentChange' :ajax='{ url: "getHistoryOrder", params: params }' ref='xTable' :tableColumn='tableColumns' maxHeight='350px' />
      </div>
      <div class="dialog-footer">
        <el-button type="primary" @click="submit()">确定</el-button>
        <el-button @click="close">取 消</el-button>
      </div>

    </vxe-modal>
  </div>
</template>

<script>
var paramsModule = {
  business_start_time: "",
  business_end_time: "",
  customer_id: "",
  customer_name: "",
  goods_id: "",
};
export default {
  data() {
    return {
      showData: {},
      params: this.$publicFution.deepCopy(paramsModule),
      timeValue: [
        this.$publicFution.get3MonthBefor(),
        this.$publicFution.getTime(new Date()),
      ],
      currentScope: "",
      tableColumns: [
        {
          title: "序号",
          type: "index",
          width: 65,
        },
        {
          title: "业务日期",
          field: "business_time",
          width: 100,
        },
        {
          title: "单据号",
          field: "outstock_sn",
        },
        {
          title: "客户名称",
          field: "customer_name",
        },
        {
          title: "数量",
          field: "real_num",
          headerAlign: "right",
          align: "right",
          width: 70,
        },
        {
          title: "单位",
          field: "unit_name",
          width: 70,
        },
        {
          title: "售价",
          field: "unit_price",
          headerAlign: "right",
          align: "right",
          width: 70,
        },
      ],
    };
  },
  props: {
    value: null,
    company_id: "",
    searchParams: {
      type: Object,
      default: () => {},
    },
  },
  methods: {
    dealData({ data }) {
      this.showData = data.data;
    },
    currentChange(scope) {
      this.currentScope = scope;
    },
    close() {
      this.$emit("input", false);
    },
    dealDate() {
      if (Array.isArray(this.timeValue)) {
        this.params.business_start_time = this.timeValue[0];
        this.params.business_end_time = this.timeValue[1];
      } else {
        this.params.business_start_time = null;
        this.params.business_end_time = null;
      }
    },
    onReset() {
      this.params = this.$publicFution.deepCopy(paramsModule);
      this.params = Object.assign(this.params, this.searchParams);
      this.onSearch();
    },
    onSearch() {
      this.dealDate();
      this.$refs.xTable._getData(1);
    },
    submit(scope) {
      var row = "";
      if (scope) {
        row = scope.row;
      } else {
        row = this.currentScope.row;
        if (!row) {
          return;
        }
      }
      this.$emit("sumbitData", row);
      this.close();
    },
  },
  created() {
    console.log(this.tableColumn);
    this.dealDate();
    this.params = Object.assign(this.params, this.searchParams);
  },
};
</script>

