        <el-form-item label="选择客户" size="mini">
          <el-select clearable v-model="params.customer_id" filterable placeholder="请选择" :disabled="pardata.outstock_id" @focus="clienteleListInvok" :loading="loading" remote :remote-method="clienteleListInvok">
