            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="部门" size="mini">
          <el-select clearable v-model="params.department_id" @focus="DepartmentListInvok" placeholder="请选择" filterable>
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="所属区域" size="mini" class="wauto">
          <el-select clearable v-model="params.area_id" filterable @visible-change="getRegionListOption()" placeholder="请选择" @change="getRegionListOptionChange" :filter-method="getRegionChange">
            <el-option :label="item.area_name" :key="item.area_id" :value="item.area_id" v-for="item in areaList.options"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="订单来源" size="mini" v-if="is_shop == 1">
          <el-select clearable v-model="params.is_erp" placeholder="请选择" filterable>
            <el-option v-for="(val, key) in $baseData.saleOrderType" :label="key" :key='key' :value="Array.isArray(val) ? val[0] : val"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="支付方式" size="mini" v-if="is_shop == 1">
          <el-select clearable v-model="params.is_pay" placeholder="请选择" filterable>
            <el-option label="全部" :value="3"></el-option>
            <el-option label="线下支付" :value="0"></el-option>
            <el-option label="线上支付" :value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商城订单号" size="mini">
          <el-input v-model="params.ec_order_sn" class="shortSearchInput"></el-input>
        </el-form-item>
        <el-form-item label="供货方式" size="mini">
          <el-select filterable clearable v-model="params.supply_mode" placeholder="请选择">
            <el-option label="全部" :value="0"></el-option>
            <el-option label="现货" :value="1"></el-option>
            <el-option label="非现货" :value="2"></el-option>
            <el-option label="其他" :value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" @row-click="selectTableData" max-height="600">
        <el-table-column :show-overflow-tooltip="true" width="50" label="选择">
          <template slot-scope="scope">
            <el-radio v-model="radio" :label="scope.row.order_id">{{null}}</el-radio>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="company_name" label="销售组织" min-width="120">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="order_sn" label="单据号" width="180">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="department_name" label="部门" min-width="100">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="customer_name" label="客户名称" min-width="120">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="num" label="商品数量" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="surplus_num" label="未出库数量" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="order_price" label="订单金额" align="right" width="120">
          <template slot-scope="scope">
            {{scope.row.order_price|priceFormat}}
          </template>
        </el-table-column>

        <el-table-column :show-overflow-tooltip="true" property="credit_price" label="本单使用授信" align="right" width="120">
          <template slot-scope="scope">
            {{scope.row.credit_price|priceFormat}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="rebate_price" label="本单使用返利" align="right" width="120">
          <template slot-scope="scope">
            {{scope.row.rebate_price|priceFormat}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="deposits_price" label="本单使用预收" align="right" width="120">
          <template slot-scope="scope">
            {{scope.row.deposits_price|priceFormat}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="settlement_price" label="本单结算金额" align="right" width="120">
          <template slot-scope="scope">
            {{scope.row.settlement_price|priceFormat}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期" width="110">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="auditor_status_name" label="订单状态">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="auditor_name" label="审核人" width="120">
        </el-table-column>
        <!-- <el-table-column :show-overflow-tooltip="true" property="order_type_name" label="订单类型" width="160">
        </el-table-column> -->
        <el-table-column :show-overflow-tooltip="true" property="ec_order_sn" label="商城订单号" width="160">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="is_erp_title" width="180" label="订单来源" v-if="is_shop == 1">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="is_pay_title" width="180" label="支付方式" v-if="is_shop == 1">
        </el-table-column>
      </el-table>
    </div>
    <div class="page_container">
      <div class="pageNumber">每页显示</div>
      <el-pagination class="erp-pagination" :current-page.sync="goPage" :page-size="pageSize1" :page-sizes="[30, 50, 100,500]" @current-change="handleCurrentChange" layout="sizes, prev, pager, next" prevText="上一页" nextText="下一页" :total="TableData.total">
      </el-pagination>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogCancelForm">取 消</el-button>
      <el-button type="primary" @click="dialogSubmitForm()">确 定</el-button>
    </div>
  </div>
</template>

<script>
import { getData, opthionList } from "common/utils/public_function/commonUtils";
import getAreaList from "common/components/public/getAreaList";
export default {
  data() {
    return {
      radio: null,
      order_id: null,
      timeValue: [],
      params: {
        cityList: [],
      },
      loading: false,
      searchParams: {},
      TableData: {
        tableData: [],
        total: 10,
      },
      level: 0,
      num: null,
      companyList: [],
      per_page: [30, 50, 100, 500],
      pageSize: 30,
      goPage: 1,
      is_shop: 0,
      areaList: {
        options: [],
      },
      defaultArea: [0], //默认的区域id
    };
  },
  props: ["pardata"],
  components: {
    getAreaList,
  },
  methods: {
    getRegionChange(val) {
      //地区搜索
      if (val) {
        this.$common.options("getAreaListOption", this.areaList, {
          area_name: val,
        });
      }
    },
    getRegionListOptionChange() {
      // 此方法无法及时切换数据，故使用下方请求方法
      this.$axios({
        method: "get",
        url: this.$api.getRegionListOption,
        params: {
          area_id: this.params.area_id,
        },
      }).then((res) => {
        if (res.data.code == 200) {
          this.areaList.options = res.data;
          this.level = res.data.data.level;
        }
      });
    },
    getRegionListOption() {
      //默认获取所有地区
      this.$common.options("getAreaListOption", this.areaList, {
        level: 2,
      });
    },
    changeDefaultArea(addressId) {
      this.params.cityList = addressId.join(",");
      this.params.province_id = addressId[0];
      this.params.city_id = addressId[1];
      this.params.area_id = addressId[2];
    },
    selectTableData(val) {
      this.radio = val.order_id;
      this.order_id = val.order_id;
      this.num = val.num;
    },
    dialogCancelForm() {
      this.$emit("closedialog", false);
    },
    dialogSubmitForm() {
      if (this.order_id) {
        this.$axios({
          method: "get",
          url: this.$api.referenceOrderInfoxs,
          params: {
            order_id: this.order_id,
            warehouse_id: this.pardata.warehouse_id,
            order_type: this.params.order_type ? this.params.order_type : 0,
          },
        }).then((res) => {
          if (res.data.code == 200) {
            this.$emit("dialogformInline", res.data.data, this.num);
          }
        });
      } else {
        this.$message.error("请选择一项");
      }
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val,
      });
      getData(this.searchParams);
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.business_start_time = this.timeValue[0];
        this.params.business_end_time = this.timeValue[1];
      } else {
        this.params.business_start_time = this.params.business_end_time = "";
      }
      this.$set(this.params, "warehouse_id", this.pardata.warehouse_id);
      Object.assign(this.searchParams.params, this.params, {
        page: 1,
        warehouse_id: this.pardata.warehouse_id,
        level: this.level,
      });
      getData(this.searchParams);
    },
    onReset() {
      this.$set(this, "params", {
        warehouse_id: this.pardata.warehouse_id,
        customer_id: this.pardata.customer_id,
        company_id: this.pardata.company_id,
        order_type: 0,
        is_erp: 4,
        supply_mode: 0,
        level: 0,
      });
      this.level = 0;
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: this.params,
        TableData: this.TableData, //列表数据
        API: "referenceOrderListPublics", //接口名
        vm: this, //传递的this
      });
      getData(this.searchParams);
    },
  },
  mounted() {
    //获取组织
    this.$set(this, "params", {
      warehouse_id: this.pardata.warehouse_id,
      customer_id: this.pardata.customer_id,
      customer_name: this.pardata.customer_name,
      company_id: this.pardata.company_id,
      order_type: 0,
      supply_mode: 0,
      is_erp: 4,
    });
    opthionList(this, this.pardata);
    this.$set(this, "searchParams", {
      params: this.params,
      TableData: this.TableData, //列表数据
      API: "referenceOrderListPublics", //接口名
      vm: this, //传递的this
    });
    getData(this.searchParams);
  },
};
</script>


